import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Grid, 
  Card, 
  CardContent, 
  CardMedia, 
  Typography, 
  Box, 
  Button, 
  Chip,
  CircularProgress,
  Paper,
  TextField,
  InputAdornment,
  IconButton,
  Fade,
  Grow,
  useTheme,
  useMediaQuery
} from '@mui/material';
import { 
  School as SchoolIcon, 
  People as PeopleIcon, 
  AccessTime as AccessTimeIcon,
  Search as SearchIcon,
  Add as AddIcon,
  TrendingUp as TrendingUpIcon,
  Star as StarIcon,
  Edit as EditIcon,
  Visibility as VisibilityIcon
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { styled, keyframes } from '@mui/system';

// Animation keyframes
const pulse = keyframes`
  0% { transform: scale(1); }
  50% { transform: scale(1.02); }
  100% { transform: scale(1); }
`;

const slideIn = keyframes`
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
`;

// Styled Components
const StyledCard = styled(Card)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  transition: 'all 0.3s ease-in-out',
  cursor: 'pointer',
  position: 'relative',
  overflow: 'hidden',
  borderRadius: '16px',
  bgcolor: 'white',
  boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
  '&:hover': {
    transform: 'translateY(-8px)',
    boxShadow: '0 12px 40px rgba(0,0,0,0.15)',
    '& .MuiCardMedia-root': {
      transform: 'scale(1.05)'
    },
    '& .action-buttons': {
      opacity: 1,
      transform: 'translateY(0)'
    }
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    height: '4px',
    background: 'linear-gradient(90deg, #3498db, #2ecc71)',
    zIndex: 1
  }
}));

const StyledCardMedia = styled(CardMedia)({
  height: 160,
  position: 'relative',
  transition: 'transform 0.5s ease-in-out',
  bgcolor: '#ecf0f1',
  '&::after': {
    content: '""',
    position: 'absolute',
    bottom: 0,
    left: 0,
    right: 0,
    height: '60%',
    background: 'linear-gradient(to top, rgba(0,0,0,0.4) 0%, transparent 100%)',
    zIndex: 1
  }
});

// Mock data - replace with actual API call
const mockCourses = [
  {
    id: 1,
    title: '   ',
    description: '       React  Node.js',
    image: 'https://via.placeholder.com/300x200/3498db/ffffff?text=Web+Dev',
    students: 45,
    duration: '8 ',
    category: '',
    progress: 75,
    rating: 4.8,
    lessons: 24,
    lastUpdated: ' '
  },
  {
    id: 2,
    title: '  JavaScript',
    description: '    JavaScript    ',
    image: 'https://via.placeholder.com/300x200/f39c12/ffffff?text=JavaScript',
    students: 32,
    duration: '6 ',
    category: '',
    progress: 90,
    rating: 4.9,
    lessons: 18,
    lastUpdated: ' '
  },
  {
    id: 3,
    title: '  ',
    description: '    SQL  MySQL',
    image: 'https://via.placeholder.com/300x200/e74c3c/ffffff?text=Database',
    students: 28,
    duration: '4 ',
    category: ' ',
    progress: 60,
    rating: 4.7,
    lessons: 12,
    lastUpdated: ' 3 '
  }
];



const MyCourses = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [courses, setCourses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    // Simulate API call
    const fetchCourses = async () => {
      try {
        // TODO: Replace with actual API call
        // const response = await fetch('/api/teacher/courses');
        // const data = await response.json();
        setCourses(mockCourses);
      } catch (error) {
        console.error('Error fetching courses:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchCourses();
  }, []);

  // Handle search input
  const handleSearch = (event) => {
    setSearchQuery(event.target.value);
  };
  
  // Filter courses based on search query
  const filteredCourses = courses.filter(course => 
    course.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
    course.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
    course.category.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleCourseClick = (courseId) => {
    navigate(`/teacher/courses/${courseId}`);
  };

  const handleCreateCourse = () => {
    navigate('/teacher/courses/new');
  };



  // Header Component
  const Header = () => (
    <Box sx={{ 
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      mb: 4,
      p: 3,
      bgcolor: 'white',
      borderRadius: 3,
      boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
      position: 'relative',
      overflow: 'hidden',
      '&::before': {
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        height: '3px',
        background: 'linear-gradient(90deg, #3498db, #2ecc71, #f39c12)',
        zIndex: 1
      }
    }}>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
        <Box sx={{ 
          display: 'flex', 
          alignItems: 'center', 
          gap: 1,
          p: 1.5,
          bgcolor: '#f8f9fa',
          borderRadius: 2
        }}>
          <SchoolIcon sx={{ color: '#3498db', fontSize: 28 }} />
          <Typography variant="h5" component="h1" fontWeight="bold" sx={{ color: '#2c3e50' }}>
            
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <TrendingUpIcon sx={{ color: '#27ae60', fontSize: 20 }} />
          <Typography variant="body2" color="text.secondary">
            {courses.length}  
          </Typography>
        </Box>
      </Box>
      
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <TextField
            variant="outlined"
            placeholder="  ..."
            value={searchQuery}
            onChange={handleSearch}
            size="small"
            sx={{
              width: 300,
              '& .MuiOutlinedInput-root': {
                borderRadius: 2,
                bgcolor: '#f8f9fa',
                '&:hover .MuiOutlinedInput-notchedOutline': {
                  borderColor: '#3498db',
                },
                '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                  borderColor: '#3498db',
                },
              },
            }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon color="action" fontSize="small" />
                </InputAdornment>
              ),
            }}
          />
          
          <Button 
            variant="contained" 
            startIcon={<AddIcon />}
            onClick={handleCreateCourse}
            sx={{
              bgcolor: '#3498db',
              color: 'white',
              borderRadius: 2,
              px: 3,
              py: 1,
              fontWeight: 'bold',
              textTransform: 'none',
              boxShadow: '0 4px 14px rgba(52, 152, 219, 0.3)',
              '&:hover': {
                bgcolor: '#2980b9',
                boxShadow: '0 6px 20px rgba(52, 152, 219, 0.4)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.3s ease'
            }}
          >
              
          </Button>
        </Box>
    </Box>
  );



  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="60vh">
        <CircularProgress />
      </Box>
    );
  }

  // Course Card Component
  const CourseCard = ({ course }) => (
    <Grow in={true} timeout={500}>
      <Grid item xs={12} sm={6} md={4} key={course.id}>
        <StyledCard onClick={() => handleCourseClick(course.id)}>
          <Box sx={{ position: 'relative' }}>
            <StyledCardMedia
              component="img"
              image={course.image}
              alt={course.title}
            />
            
            {/* Action Buttons */}
            <Box 
              className="action-buttons"
              sx={{ 
                position: 'absolute',
                top: 10,
                left: 10,
                display: 'flex',
                gap: 1,
                opacity: 0,
                transform: 'translateY(-10px)',
                transition: 'all 0.3s ease',
                zIndex: 2
              }}
            >
              <IconButton 
                size="small" 
                sx={{ 
                  bgcolor: 'rgba(255,255,255,0.9)',
                  '&:hover': { bgcolor: 'white' }
                }}
                onClick={(e) => {
                  e.stopPropagation();
                  navigate(`/teacher/courses/${course.id}/edit`);
                }}
              >
                <EditIcon fontSize="small" />
              </IconButton>
              <IconButton 
                size="small" 
                sx={{ 
                  bgcolor: 'rgba(255,255,255,0.9)',
                  '&:hover': { bgcolor: 'white' }
                }}
                onClick={(e) => {
                  e.stopPropagation();
                  navigate(`/teacher/courses/${course.id}`);
                }}
              >
                <VisibilityIcon fontSize="small" />
              </IconButton>
            </Box>
            
            {/* Category Chip */}
            <Box sx={{ position: 'absolute', top: 10, right: 10, zIndex: 2 }}>
              <Chip 
                label={course.category} 
                size="small" 
                sx={{ 
                  fontWeight: 'bold',
                  bgcolor: '#3498db',
                  color: 'white',
                  fontSize: '0.7rem'
                }}
              />
            </Box>
          </Box>
          
          <CardContent sx={{ flexGrow: 1, p: 2 }}>
            <Typography 
              gutterBottom 
              variant="h6" 
              component="h2" 
              sx={{
                fontWeight: 'bold',
                minHeight: '2.5em',
                display: '-webkit-box',
                WebkitLineClamp: 2,
                WebkitBoxOrient: 'vertical',
                overflow: 'hidden',
                lineHeight: '1.3em',
                textAlign: 'right',
                fontSize: '1rem',
                mb: 1
              }}
            >
              {course.title}
            </Typography>
            
            <Typography 
              variant="body2" 
              color="text.secondary" 
              sx={{
                display: '-webkit-box',
                WebkitLineClamp: 2,
                WebkitBoxOrient: 'vertical',
                overflow: 'hidden',
                mb: 2,
                minHeight: '2.5em',
                textAlign: 'right',
                fontSize: '0.85rem'
              }}
            >
              {course.description}
            </Typography>
            
            {/* Course Stats */}
            <Box sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              justifyContent: 'space-between',
              mb: 2,
              flexWrap: 'wrap',
              gap: 1
            }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <PeopleIcon color="action" fontSize="small" />
                <Typography variant="caption" color="text.secondary" sx={{ fontSize: '0.75rem' }}>
                  {course.students} 
                </Typography>
              </Box>
              
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <AccessTimeIcon color="action" fontSize="small" />
                <Typography variant="caption" color="text.secondary" sx={{ fontSize: '0.75rem' }}>
                  {course.duration}
                </Typography>
              </Box>
              
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                <StarIcon sx={{ color: '#f39c12', fontSize: '0.9rem' }} />
                <Typography variant="caption" color="text.secondary" sx={{ fontSize: '0.75rem' }}>
                  {course.rating}
                </Typography>
              </Box>
            </Box>
            
            {/* Lessons Count */}
            <Box sx={{ mb: 2 }}>
              <Typography variant="caption" color="#3498db" fontWeight="bold" sx={{ fontSize: '0.75rem' }}>
                {course.lessons} 
              </Typography>
            </Box>
            
            {/* Last Updated */}
            <Typography variant="caption" color="text.secondary" sx={{ fontSize: '0.7rem' }}>
               : {course.lastUpdated}
            </Typography>
          </CardContent>
        </StyledCard>
      </Grid>
    </Grow>
  );

  return (
    <Box sx={{ 
      minHeight: '100vh',
      bgcolor: '#f8f9fa',
      direction: 'rtl'
    }}>
      <Container maxWidth="xl" sx={{ py: 3 }}>
        {/* Header */}
        <Header />
        
        {/* Main Content */}
        {filteredCourses.length === 0 ? (
          <Fade in={true} timeout={500}>
            <Paper 
              elevation={0} 
              sx={{ 
                p: { xs: 3, md: 6 }, 
                textAlign: 'center', 
                bgcolor: 'white',
                borderRadius: 3,
                boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
                maxWidth: '600px',
                mx: 'auto',
                my: 4
              }}
            >
              <Box sx={{ 
                width: 120, 
                height: 120, 
                mx: 'auto', 
                mb: 3,
                position: 'relative',
                '&::before': {
                  content: '""',
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  borderRadius: '50%',
                  background: 'linear-gradient(135deg, rgba(52, 152, 219, 0.1) 0%, rgba(46, 204, 113, 0.1) 100%)',
                  animation: `${pulse} 2s infinite ease-in-out`,
                }
              }}>
                <SchoolIcon 
                  color="disabled" 
                  sx={{ 
                    fontSize: 60, 
                    position: 'relative',
                    top: '50%',
                    transform: 'translateY(-50%)'
                  }} 
                />
              </Box>
              <Typography variant="h6" color="text.secondary" gutterBottom>
                {searchQuery ? '  ' : '    '}
              </Typography>
              <Typography variant="body1" color="text.secondary" sx={{ mb: 3, maxWidth: '400px', mx: 'auto' }}>
                {searchQuery 
                  ? '      .    .'
                  : '        "  "'
                }
              </Typography>
              <Button 
                variant="contained" 
                startIcon={<AddIcon />}
                onClick={handleCreateCourse}
                sx={{
                  bgcolor: '#3498db',
                  color: 'white',
                  borderRadius: 2,
                  px: 4,
                  py: 1,
                  fontWeight: 'bold',
                  textTransform: 'none',
                  boxShadow: '0 4px 14px rgba(52, 152, 219, 0.3)',
                  '&:hover': {
                    bgcolor: '#2980b9',
                    boxShadow: '0 6px 20px rgba(52, 152, 219, 0.4)',
                    transform: 'translateY(-1px)'
                  },
                  transition: 'all 0.3s ease'
                }}
              >
                  
              </Button>
            </Paper>
          </Fade>
        ) : (
          <Grid container spacing={3}>
            {filteredCourses.map((course, index) => (
              <CourseCard key={course.id} course={course} />
            ))}
          </Grid>
        )}
      </Container>
    </Box>
  );
};

export default MyCourses;
